import React, { Component } from 'react';
import PropTypes from 'prop-types';
import {FontAwesomeIcon} from '@fortawesome/react-fontawesome';
import {faTimes} from '@fortawesome/free-solid-svg-icons'

import './Task.css';

class Task extends Component {


    styleTask = () => {
        return(
           this.props.task.done ? "line done" : "default"
        )
    }

    checked = () => {
        return(
        this.props.task.done ? true : false
        )
    }


    render() {

        const {task} = this.props;


        return(
            <div onDoubleClick={this.props.checkDone.bind(this, task.id)}  className={"div_padre "+this.styleTask()}> 
                <input checked={this.checked()}  onChange={this.props.checkDone.bind(this, task.id)} type="checkbox"/>
                <div className="div_hijo">
                    <h3> {task.title}</h3>
                    <p> {task.description}</p>
                </div>
                {/* <button > X </button> */}
               <div className="ex d-flex align-items-center justify-content-center"> 
                   <FontAwesomeIcon icon={faTimes} onClick={this.props.deleteTask.bind(this, task.id)} className="" title="Eliminar tarea" /> 
                </div>
            </div>
        )
        

       
    }
}

Task.propTypes = {
    task: PropTypes.object.isRequired,
}


export default Task;