import React, {Component} from 'react';

import './Form.css';

var status = "d-none";
var infoStatus;

class TaskForm extends Component {

    state = {
        title: "",
        description: ""
    }

    onSubmit = (e) => {
  
        e.preventDefault();

        if(this.state.title === "") {
            alert("No puedes crear una tarea sin nombre");
        }else {
            status = "d-none";
            this.props.newTask(this.state.title, this.state.description);
            this.setState({title:"",description:""});    
        }
        
        
    }

 
    onChange = e => {
        
        this.setState({
            [e.target.name]: e.target.value
        })

        if(e.target.name === "title") {

            if(e.target.value === "") {
                status = "error";
                infoStatus = "Necesitas escribir el nombre de la tarea";
            } else {
                status = "d-none";
            }
        }

        
        if(e.target.name === "description") {
            if(this.state.title === "") {
                status = "error";
                infoStatus = "Necesitas escribir el nombre de la tarea";
            } else {
                status = "d-none";
            }
        }


    }

    onKeyDown = (e) => {

       if(e.keyCode === 13) {
           this.onSubmit(e);
       }


    }

    ocultarNota = (e) => {
        document.querySelector(".nota").style.display = "none";
        document.querySelector(".form-group").classList.add("mt-5");
    }



    render() {

        return(
            <React.Fragment>
                
                <h6 className="nota mt-5 text-center mb-2 p-0 text-muted"> <b style={{color:"red"}}> Nota: </b> Las tareas no se guardaran en un cambio o recarga de página ya que los datos no se están alojando en un servidor. <button onClick={this.ocultarNota} className="esconderNota"> x </button> </h6>

                <form  className="form-group container d-flex flex-wrap align-items-center justify-content-center flex-column" onSubmit={this.onSubmit}>
    
                    <h1 className="text-center mb-3"> Agregar una tarea </h1>
    
                    <input 
                        name="title"
                        type="text"
                        placeholder="Escribe una Tarea"
                        onKeyDown={this.onKeyDown}
                        onChange={this.onChange}
                        value={this.state.title}
                        className="col-6 form-control"
                        />
    
                    <textarea
                        name="description"
                        placeholder="Escribe una descripción de la tarea"
                        onChange={this.onChange}
                        value={this.state.description} 
                        className="col-6 form-control mt-1"
                        onKeyDown={this.onKeyDown}
                    >
                    </textarea>

                    <div className={"col-6 mt-3  "+ status}> 
                        <h6 className="text-center"> { infoStatus } </h6>
                    </div>
    
                    <br/>
                    <input type="submit" value="Crear tarea" />
                </form>
    
               
            </React.Fragment>
        )
    }

}

export default TaskForm;
