import React, { Component } from 'react';

import './Posts.css';

export default class Post extends Component {
    render() {

        const {post} = this.props;

        return (
           <React.Fragment>
                <div className="col-5 px-0 mr-2 mt-2 post">
                    <p className="id text-center"> {post.id} </p>
                    <h6 className="title text-center mt-4 px-2 font-weight-bold"> {post.title} </h6>
                    <p className="body text-justify px-3 mt-4"> {post.body} </p>
                </div>
           </React.Fragment>
        )
    }
}
