import React, { Component } from 'react'

import Post from './Post';

export default class Posts extends Component {

    state = {
        posts: []
    }

    async componentDidMount() {

        const res = await fetch("https://jsonplaceholder.typicode.com/posts");
        const data = await res.json();

        this.setState({posts: data});

    }

    
    
    render() {

        
    const estilosPosts = {
        color: "gray",
        border: "1px solid gray",
        padding: "20px",
        borderRadius: "15px",
        width: "fit-content"
    }
        
      
    return (

          <React.Fragment>

                <div className="container text-center d-flex align-items-center justify-content-center mt-5 mb-2">
                    <h6 style={estilosPosts}> Posts recopilados con Fetch() desde: https://jsonplaceholder.typicode.com/posts </h6>
                </div>

               <section className="container d-flex align-items-center justify-content-center flex-wrap">     
                    {this.state.posts.map(post => { return <Post post={post} /> }) }
                </section>
          </React.Fragment>
       )


    }



}