import React, { Component } from 'react';

import Task from './Task';


class Tasks extends Component {


    render() {
        
        if(this.props.tasks.length === 0) {
            return (
                <div className="container"> 
                    <h6 className="text-muted text-center font-weight-bold"> Aún no has creado ninguna tarea, agregala en el campo que está arriba. </h6>
                </div> 
                )
        } else {
            return (
                <div className="container d-flex align-items-center justify-content-center flex-wrap flex-column">
                    <h6 className="text-muted text-center"> <b>TIP:</b> Puedes marcar como cumplida una tarea haciendo doble click en ella. </h6>
                    {this.props.tasks.map(task => <Task task={task} key={task.id} deleteTask={this.props.deleteTask} checkDone={this.props.checkDone}/> )}
                </div>
            )
        } 
      
        
    }
    
}


export default Tasks;