import React, { Component } from 'react';
import { Link } from 'react-router-dom';


import './Navbar.css';
import LogoReact from '../images/logo_react.svg';

// var active = {color:"orange"};
var active ;
class Navbar extends Component {


    render() {

        return (
            <nav>  

               <div className="h-100 container row m-auto d-flex align-items-center justify-content-between">
                   
                    <div className="h-100 col-4 d-flex align-items-center justify-content-end">
                        <img className="d-flex h-100" src={LogoReact} alt="Logo React"/>
                        <h6 className="spa ml-2" > Single Page App (SPA) </h6>
                    </div>
    
                    <div className="h-100 col-8 d-flex align-items-center justify-content-end">
                        <Link style={active} className={"link position-relative mr-3 "} to="/App-React"> Aplicación de Tareas </Link>
                        <Link className="link position-relative mr-3" to="/Posts"> Posts </Link>
                        <Link className="link position-relative" to="/VideoGames"> Juegos </Link>
                    </div>
               </div>

            </nav>
        )
    }
}


export default Navbar;
