import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import React, { Component } from 'react';
import {Button,Modal,ModalBody,ModalFooter,ModalHeader} from 'reactstrap';
import {faTimes} from '@fortawesome/free-solid-svg-icons';

import './VideoGame.css';


export default class VideoGames extends Component {

    state = {
        stateModal: false
    }

    changeModalState = () => {
        this.setState({stateModal: !this.state.stateModal});
        console.log("me diste click")
    }


    render() {

        const {game} = this.props;

        return (
            <div onClick={this.changeModalState} className="custom_card col-12 col-md-5 col-lg-3 col-xl-3 mx-2 mb-5" title="Haz click para ver video">

                <h6 id="title" className="text-center"> {game.name} </h6>

                <div className="div_imagen">
                  <img className="w-100 h-100" src={game.image} alt={game.alt} />
                </div>

                <div id="description" className="px-5 my-3 ">
                    <p> {game.description} </p> 
                </div>

               

                <Modal isOpen={this.state.stateModal}>
                    <Button className="boton_modal" onClick={this.changeModalState}>
                        <FontAwesomeIcon className="icono" icon={faTimes} />
                    </Button>
                    <ModalBody style={{background:"transparent"}}>
                        <iframe height="500" width="100%" src={game.video} />
                   </ModalBody>
                </Modal>

            </div>
        )
    }
}
