import React, { Component } from 'react'

import games from '../json/videogames.json';

import VideoGame from './VideoGame';



export default class VideoGames extends Component {

    state = {
        games: games
    }


    render() {

       return (
            <React.Fragment>
                <h6 className="mt-5 text-center"> Haz click en cualquier tarjeta para ver su video correspondiente </h6>
               <div className="d-flex align-items-center justify-content-center flex-wrap mt-5">
                    {this.state.games.map(game =>  <VideoGame key={game.id} game={game}/> )}
                </div>
            </React.Fragment>
        )

       
    }

}

